<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">数据提取</span>
      <span class="topTitle-span1">
        <img src="../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">预览查看</span>
          <div class="button-content">
            <Button type="primary" @click="goBack">
              <i class="md-arrow-round-up"></i>返回
            </Button>
          </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellTitle">
        <div class="contentCellTitle-1"></div>基本信息
      </div>
      <div class="contentCellBody">
        <Row class="flexStart">
          <div class="baseInformationDiv flexStart">
            <div class="baseInformationDiv-1">执行开始时间:</div>
            <div class="baseInformationDiv-2">{{baseArr.execuStartTime}}</div>
          </div>
          <div class="baseInformationDiv flexStart">
            <div class="baseInformationDiv-1">执行结束时间:</div>
            <div class="baseInformationDiv-2">{{baseArr.execuEndTime?baseArr.execuEndTime.substr(0,19):''}}</div>
          </div>
          <div class="baseInformationDiv flexStart">
            <div class="baseInformationDiv-1">执行状态:</div>
            <div
              class="baseInformationDiv-2"
              style="color:RGB(56,218,167)"
            >运行结束</div>
          </div>
        </Row>
        <Row class="flexStart">
          <div class="baseInformationDiv flexStart">
            <div class="baseInformationDiv-1">成功数据量:</div>
            <div class="baseInformationDiv-2">{{baseArr.succNum}}</div>
          </div>
          <div class="baseInformationDiv flexStart">
            <div class="baseInformationDiv-1">错误数据量:</div>
            <div class="baseInformationDiv-2">{{baseArr.errorNum}}</div>
          </div>
        </Row>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBody">
        <Tabs :value="tabFlag" @on-click="againSubmit">
          <TabPane label="成功数据量" name="success">
            <div class="contentCellBox">
              <div class="contentCellTitle tableTitle">预览</div>
              <div class="contentCellBody">
                <Row>
                  <table class="tableBox">
                    <thead>
                      <tr>
                        <td>序号</td>
                        <td v-for="(item,index) in tableheader1">
                          <p>{{item.tableFieldName}}</p>
                          <p v-if="item.tableField!==''">({{item.tableField}})</p>
                        </td>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in tablebody1" :key="index">
                        <td>{{pageSize*(pageNum-1)+index+1}}</td>
                        <template v-for="item2 in tableFields">
                          <template v-if="item[item2]">
                            <td>{{item[item2][item2]}}</td>
                          </template>
                        </template>
                      </tr>
                    </tbody>
                  </table>
                </Row>
              </div>
              <div class="flexCenter pageBox">
                <Page :current.sync="pageNum" :total="succNum" :page-size="pageSize" @on-change="pageChange" @on-page-size-change='pageSizefun' show-elevator show-sizer/>
              </div>
            </div>
          </TabPane>
          <TabPane label="错误数据量" name="error">
            <div class="contentCellBox">
              <div class="contentCellTitle tableTitle">
                预览
                <!-- <Button
                  type="primary"
                  icon="ios-download-outline"
                  slot="extra"
                  size="small"
                  class="rightTopbtn"
                >导出</Button> -->
              </div>
              <div class="contentCellBody">
                <Row>
                  <table class="tableBox">
                    <thead>
                      <tr>
                        <td>序号</td>
                        <td v-for="(item,index) in tableheader1" :key="index">
                          <p>{{item.tableFieldName}}</p>
                          <p v-if="item.tableField!==''">({{item.tableField}})</p>
                        </td>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in tablebody1">
                        <td>{{pageSize*(pageNum-1)+index+1}}</td>
                        <template v-for="item2 in tableFields">
                            <td v-if="item[item2]['flag']===false">{{item[item2][item2]}}</td>
                            <td v-if="item[item2]['flag']===true">
                              <Tooltip placement="top">
                                <p style="color:red;cursor: pointer;">{{item[item2][item2]}}</p>
                                <div slot="content">
                                  <p>{{item[item2]['errorContent']}}</p>
                                </div>
                              </Tooltip>
                            </td>
                        </template>
                      </tr>
                    </tbody>
                  </table>
                </Row>
              </div>
              <div class="flexCenter pageBox">
                <Page :current.sync="pageNum" :total="errorNum" :page-size="pageSize" @on-change="pageChange" @on-page-size-change='pageSizefun' show-elevator show-sizer/>
              </div>
            </div>
          </TabPane>
        </Tabs>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
    let that = this;
    this.tabFlag = this.$route.query.type;
    this.batchId = this.$route.query.batchId;
    this.isSuc = this.$route.query.isSuc;
    this.reDoNum = this.$route.query.reDoNum;
    this.tableName = this.$route.query.tableName;
    this.succNum =  this.$route.query.succNum*1;
    this.errorNum =  this.$route.query.errorNum*1;
    this.axios
        .get(`/extract/opt/extractDetailOpt/${this.batchId}/${this.tableName}/${this.isSuc}/${this.reDoNum}/${this.pageSize}/${this.pageNum}`)
          .then(response => {
            if (response.code=="200"){
              that.baseArr = response.data.basicData;
              that.tableheader1 = response.data.tableDetails;
              that.tableFields = [];
              for(let j=0;j<that.tableheader1.length;j++){
                  that.tableFields.push(that.tableheader1[j].tableField);
              }
              that.tablebody1 = response.data.recordDatas;
            }
        });
  },
  methods: {
    goBack(){
      this.$router.go(-1)
    },
    pageChange(pageNo) {
      this.pageNum = pageNo * 1;
      let that = this;
      this.axios
        .get(`/extract/opt/extractDetailOpt/${this.batchId}/${this.tableName}/${this.isSuc}/${this.reDoNum}/${this.pageSize}/${this.pageNum}`)
          .then(response => {
            if (response.code=="200"){
              that.baseArr = response.data.basicData;
              that.tableheader1 = response.data.tableDetails;
              that.tableFields = [];
              for(let j=0;j<that.tableheader1.length;j++){
                  that.tableFields.push(that.tableheader1[j].tableField);
              }
              that.tablebody1 = response.data.recordDatas;
            }
        });
    },
    pageSizefun(a){
      this.pageSize=a*1;
      if(this.pageNum == 1){
          this.pageChange(1);
      }
    },
    againSubmit(name) {
        let that = this;
        this.isSuc = (name=="success"?"0":1);
        this.axios
        .get(`/extract/opt/extractDetailOpt/${this.batchId}/${this.tableName}/${this.isSuc}/${this.reDoNum}/${this.pageSize}/${this.pageNum}`)
          .then(response => {
            if (response.code=="200"){
              that.baseArr = response.data.basicData;
              that.tableheader1 = response.data.tableDetails;
              that.tableFields = [];
              for(let j=0;j<that.tableheader1.length;j++){
                  that.tableFields.push(that.tableheader1[j].tableField);
              }
              that.tablebody1 = response.data.recordDatas;
            }
        });
    }
  },
  data() {
    return {
      succNum: 0,
      errorNum:0,
      pageSize:10,
      pageNum: 1,
      tabFlag: "success",
      batchId: "",
      isSuc: "",
      reDoNum: "",
      tableName: "",
      baseArr: {
      },
      tableheader1: [
      ],
      tableFields:[
      ],
      tablebody1: [
        // {
        //   capital_acc_no:{ capital_acc_no: "张万里", flag: false },
        //   cst_no:{ cst_no: "Bj001", flag: true, content: "错误信息XXXXXXX999999999999" },
        //   cst_name:{ cst_name: "001", flag: false },
        //   ticd:{ ticd: "ADSDF0001", flag: false },
        //   date:{ date: "分公司", flag: false },
        //   amt:{ amt: "境内", flag: false }
        // },
      ]
    };
  }
};
</script>
<style scoped>
.baseInformationDiv {
  width: 400px;
  height: 30px;
  font-size: 16px;
  color: RGB(149, 149, 149);
}
.baseInformationDiv-1 {
  width: 50%;
  height: 30px;
  text-align: right;
  box-sizing: border-box;
  padding: 0 5px;
  line-height: 30px;
  color: RGB(88, 88, 88);
}
.baseInformationDiv-2 {
  width: 50%;
  height: 30px;
  text-align: left;
  padding: 0 5px;
  box-sizing: border-box;
  line-height: 30px;
}
.tableTitle {
  color: black;
  font-weight: 200;
  font-size: 18px;
}
.rightTopbtn {
  margin: 4px 4px;
  float: right;
}
</style>>


